<script>
import Konva from "konva";
const width = window.innerWidth;
const height = window.innerHeight;
const generateTargets = () => {
  console.log(width,height);
  const images = [];
  for (let i = 0; i < 8; i++) {
    images.push({
      id: i,
      x: i < 3 ? width * 0.2 : width * 0.5,
      y: i < 3 ? 10 * 0.5 * i : height * 0.3 * (i - 2),
      image: "",
      width: i < 3 ? 100:250,
      height: i < 3 ? 150 : 50,
    });
    if(i==5 || i==7){
      images[i].width=100;
    }
  }
  return images;
};
export default {
  created() {
    for (let i = 0; i < 8; i++) {
      let image = new window.Image();
      image.src = require(`@/assets/images/activities-assets/activity_2-1/r${1 + i}.png`);
      image.onload = () => {
        this.targets[i].image = image;
      };
    }
  },
  data() {
    return{
      drawningLine: false,
      changeLineStartingPoint: [],
      changeLine: false,
      selected: [],
      targets: generateTargets(),
      stageSize: {
        width: width - 800,
        height: height - 300,
      },
      connections: [],
      tableAnswer:[
        ["",""],
        ["",""],
        ["",""],
        ["",""],
        ["",""],
        ["",""],
      ],
      pupi:[
        ['P','L','A','N','C','H','A','R','E','K','E','A'],
        ['E','T','U','C','R','E','A','N','D','O','P','N'],
        ['N','Q','S','U','B','L','I','M','Y','R','I','G'],
        ['G','A','G','I','V','R','L','R','E','C','O','H'],
        ['R','S','B','E','S','A','O','N','C','P','T','A'],
        ['A','V','S','J','U','T','P','K','O','Y','J','O'],
        ['N','V','E','J','C','L','R','L','D','B','V','R'],
        ['A','I','V','E','V','T','E','D','G','L','F','U'],
        ['X','G','N','P','P','A','S','V','E','C','N','G'],
        ['L','O','T','H','L','E','G','O','J','E','J','E'],
        ['C','G','O','A','N','O','H','G','O','F','I','S'],
      ]
    }
  },
  methods:{
    getImage(i){
      return require(`@/assets/images/activities-assets/activity_2-1/${i+1}.png`);
    },
    onClick(i,e){
      if(i==2){
        e.target.className = 'selected';
      }else{
        e.target.className = e.target.className == 'white' ? 'selected' : 'white';
      }
    },
    handleMouseDown(e) {
      const onCircle = e.target instanceof Konva.Image;
      if (!onCircle || e.target.id() >= 3) {
        return;
      }
      if (e.target.id() < 3 && this.drawningLine) {
        this.connections.pop();
      }
      this.drawningLine = true;
      this.changeLine = false;
      this.connections.forEach((point) => {
        if (
          point.points[0] == e.target.x() + e.target.width() / 2 &&
          point.points[1] == e.target.y() + e.target.height() / 2
        ) {
          this.changeLine = true;
          this.changeLineStartingPoint = point;
        }
      });
      this.connections.push({
        id: Date.now(),
        points: [
          e.target.x() + e.target.width() / 2,
          e.target.y() + e.target.height() / 2,
        ],
      });
    },
    handleMouseUp(e) {
      const onCircle = e.target instanceof Konva.Image;
      if (!onCircle) {
        return;
      }
      if (e.target.id() < 3 && this.drawningLine) {
        this.connections.pop();
        this.drawningLine = false;
        return;
      }
      this.drawningLine = false;
      let lastLine;
      if (this.changeLine) {
        lastLine = this.changeLineStartingPoint;
        this.connections.pop();
      } else {
        lastLine = this.connections[this.connections.length - 1];
      }
      lastLine.points = [
        lastLine.points[0],
        lastLine.points[1],
        e.target.x() + e.target.width() / 2,
        e.target.y() + e.target.height() / 2,
      ];
    },
    handleMouseMove(e) {
      if (!this.drawningLine) {
        return;
      }
      const pos = e.target.getStage().getPointerPosition();
      const lastLine = this.connections[this.connections.length - 1];
      lastLine.points = [lastLine.points[0], lastLine.points[1], pos.x, pos.y];
    },
  },
  mounted(){
    const canvas = document.querySelector(".canvas");
    const observer = new ResizeObserver(() => {
      this.stageSize.width = canvas.offsetWidth;
      this.stageSize.height = canvas.offsetHeight;
      this.targets.forEach((img, index) => {
        this.connections.forEach((point) => {
          if (
            point.points[0] == img.x + img.width / 2 ||
            point.points[1] == img.y + img.height / 2
          ) {
            point.points[0] = this.stageSize.width * 0.1 + img.width / 2;
            point.points[1] =
              this.stageSize.height * 0.15 * index + img.height / 2;
          }
          if (
            point.points[2] == img.x + img.width / 2 ||
            point.points[3] == img.y + img.height / 2
          ) {
            point.points[2] = this.stageSize.width * 0.7 + img.width / 2;
            point.points[3] =
              this.stageSize.height * 0.2 * (index - 4) + img.height / 2;
          }
        });
        if(this.stageSize.width <= 425){
          img.width = index < 3 ? 40:40;
          img.height =index < 3 ? 40:25
          img.x =
            index < 3 ? this.stageSize.width * 0.2 : this.stageSize.width * 0.6;
          img.y =
            index < 3
              ? this.stageSize.height * 0.35 * index
              : this.stageSize.height * 0.16 * (index - 2);
          this.stageSize.height = 150;
        }
        else if (this.stageSize.width <= 768 && this.stageSize.width >= 426) {
          img.width = index < 3 ? 80:120;
          img.height =index < 3 ? 130:50;
          img.x =
            index < 3 ? this.stageSize.width * 0.2 : this.stageSize.width * 0.6;
          img.y =
            index < 3
              ? this.stageSize.height * 0.35 * index
              : this.stageSize.height * 0.15 * (index - 2);
           this.stageSize.height = 500;
        }
        else if(this.stageSize.width >= 769 &&  this.stageSize.width <= 1240){
          img.width = index < 3 ? 100:140;
          img.height =index < 3 ? 150:70;
          img.x =
            index < 3 ? this.stageSize.width * 0.2 : this.stageSize.width * 0.7;
          img.y =
            index < 3
              ? this.stageSize.height * 0.35 * index
              : this.stageSize.height * 0.15 * (index - 2);
           this.stageSize.height = 500;
        }
        else if(this.stageSize.width >= 1241){
          img.width = index < 3 ? 160:180;
          img.height =index < 3 ? 210:110;
          img.x =
            index < 3 ? this.stageSize.width * 0.2 : this.stageSize.width * 0.7;
          img.y =
            index < 3
              ? this.stageSize.height * 0.35 * index
              : this.stageSize.height * 0.15 * (index - 2);
          this.stageSize.height = 900;
        }
      });
    });
    observer.observe(canvas);
  },
}
</script>

